import jsYaml from 'js-yaml';

import {
    attachDependencies,
    addCall,
    passSourceAndProps,
} from '../helpers/injector';

import {
    isValidRedirectRule,
    isAdgRedirectResourceRule,
    isAbpRewriteResourceRule,
    isUboRedirectResourceRule,
} from '../helpers/validator';

import {
    convertUboRedirectToAdg,
    convertAbpRedirectToAdg,
    convertRedirectToAdg,
    convertAdgRedirectToUbo,
} from '../helpers/converter';

import * as redirectsList from './redirectsList';


const getRedirectByName = (redirectsList, name) => {
    // eslint-disable-next-line compat/compat
    const redirects = Object.values(redirectsList);
    return redirects.find((r) => r.names && r.names.indexOf(name) > -1);
};

const getRedirectCode = (name) => {
    const redirect = getRedirectByName(redirectsList, name);
    let result = attachDependencies(redirect);
    result = addCall(redirect, result);

    return passSourceAndProps({ name }, result);
};

export const redirectsCjs = {
    getCode: getRedirectCode,
    isAdgRedirectResourceRule,
    isUboRedirectResourceRule,
    isAbpRewriteResourceRule,
    convertUboRedirectToAdg,
    convertAbpRedirectToAdg,
    convertRedirectToAdg,
    isValidRedirectRule,
    convertAdgRedirectToUbo,
};

/**
 * Redirect - object used to redirect some requests
 * e.g.
 * {
 *      title: 1x1-transparent.gif
 *      comment: http://probablyprogramming.com/2009/03/15/the-tiniest-gif-ever
 *      contentType: image/gif;base64
 *      content: R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==
 * }
 * @typedef {Object} Redirect
 * @property {string} title
 * @property {string} comment
 * @property {string} content
 * @property {string} contentType
 */

class Redirects {
    /**
     * Converts rawYaml into JS object with sources titles used as keys
     * @param rawYaml
     * @returns {Object<Redirect>} - return object with titles in the keys and RedirectSources
     * in the values
     */
    constructor(rawYaml) {
        try {
            const arrOfRedirects = jsYaml.safeLoad(rawYaml);
            this.redirects = arrOfRedirects.reduce((acc, redirect) => {
                return {
                    ...acc,
                    [redirect.title]: redirect,
                };
            }, {});
        } catch (e) {
            // eslint-disable-next-line no-console
            console.log(`Was unable to load YAML into JS due to: ${e.message}`);
            throw e;
        }
    }

    /**
     * Returns redirect source object
     * @param {string} title
     * @return {Redirect}
     */
    getRedirect(title) {
        if (Object.prototype.hasOwnProperty.call(this.redirects, title)) {
            return this.redirects[title];
        }

        // look title among aliases
        const values = Object.keys(this.redirects)
            .map((key) => this.redirects[key]);
        return values.find((redirect) => {
            const { aliases } = redirect;
            if (!aliases) {
                return false;
            }
            return aliases.indexOf(title) > -1;
        });
    }
}

export default Redirects;
